'use client';

import { useEffect, useState } from 'react';
import { usersAPI, clientsAPI, User, Client } from '@/lib/api';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Modal } from '@/components/ui/Modal';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';

export default function AdminUsersPage() {
  const { user } = useAuth();
  const router = useRouter();
  const [users, setUsers] = useState<User[]>([]);
  const [clients, setClients] = useState<Client[]>([]);
  const [selectedDoctor, setSelectedDoctor] = useState<{ id: string; name: string; patients: any[] } | null>(null);
  const [isLoadingPatients, setIsLoadingPatients] = useState(false);
  const [editingLimits, setEditingLimits] = useState<string | null>(null);
  const [limitForm, setLimitForm] = useState({ imageConversionLimit: 10, canShowImprovementPlans: true });
  
  // Modal state
  const [modal, setModal] = useState<{
    isOpen: boolean;
    title: string;
    message: string;
    type: 'success' | 'error' | 'warning' | 'info' | 'confirm';
    onConfirm?: () => void;
  }>({
    isOpen: false,
    title: '',
    message: '',
    type: 'info'
  });

  const showModal = (
    title: string,
    message: string,
    type: 'success' | 'error' | 'warning' | 'info' | 'confirm' = 'info',
    onConfirm?: () => void
  ) => {
    setModal({ isOpen: true, title, message, type, onConfirm });
  };

  const closeModal = () => {
    setModal({ ...modal, isOpen: false });
  };

  useEffect(() => { if (user?.role !== 'admin') router.push('/dashboard'); }, [user, router]);
  useEffect(() => { (async () => { const res = await usersAPI.list(); setUsers(res.data as any); const c = await clientsAPI.list(); setClients(c.data); })(); }, []);

  const clientName = (id?: string) => clients.find(c => c._id === id)?.name || 'â€”';

  const loadDoctorPatients = async (doctorId: string, doctorName: string) => {
    setIsLoadingPatients(true);
    try {
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/patients?doctorId=${doctorId}`, {
        headers: { 'Authorization': `Bearer ${localStorage.getItem('token')}` }
      });
      const patients = await response.json();
      setSelectedDoctor({ id: doctorId, name: doctorName, patients: patients || [] });
    } catch (error) {
      console.error('Failed to load doctor patients:', error);
    } finally {
      setIsLoadingPatients(false);
    }
  };

  const openLimitEditor = (u: any) => {
    setEditingLimits(u.id);
    setLimitForm({
      imageConversionLimit: u.imageConversionLimit || 10,
      canShowImprovementPlans: u.canShowImprovementPlans !== false
    });
  };

  const saveLimits = async (userId: string) => {
    try {
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/users/${userId}/limits`, {
        method: 'PATCH',
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(limitForm)
      });
      if (response.ok) {
        const updated = await response.json();
        setUsers(users.map(u => u.id === userId ? { ...u, ...updated } : u));
        setEditingLimits(null);
        showModal('Success', 'Limits updated successfully', 'success');
      } else {
        showModal('Error', 'Failed to update limits', 'error');
      }
    } catch (error) {
      console.error('Error updating limits:', error);
      showModal('Error', 'Error updating limits', 'error');
    }
  };

  const resetUsage = async (userId: string) => {
    showModal(
      'Confirm Reset',
      'Are you sure you want to reset the usage count for this doctor?',
      'confirm',
      async () => {
        try {
          const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/users/${userId}/reset-usage`, {
            method: 'POST',
            headers: { 'Authorization': `Bearer ${localStorage.getItem('token')}` }
          });
          if (response.ok) {
            const res = await usersAPI.list();
            setUsers(res.data as any);
            showModal('Success', 'Usage reset successfully', 'success');
          }
        } catch (error) {
          console.error('Error resetting usage:', error);
          showModal('Error', 'Failed to reset usage', 'error');
        }
      }
    );
  };

  return (
    <div className="space-y-6">
      {/* Header Section */}
      <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-4">
        <div>
          <h2 className="text-3xl font-bold text-gray-900">User Management</h2>
          <p className="text-sm text-gray-600 mt-1">Manage doctors, admins, and their permissions</p>
        </div>
        <Button 
          onClick={() => router.push('/dashboard/admin/users/new')}
          className="bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white px-6 py-2.5 rounded-lg shadow-md hover:shadow-lg transition-all duration-200 flex items-center gap-2"
        >
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
          </svg>
          Add New User
        </Button>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Users Table */}
        <div className="lg:col-span-2">
          <Card className="shadow-lg border-0">
            <CardHeader className="bg-gradient-to-r from-gray-50 to-gray-100 border-b">
              <div className="flex items-center gap-3">
                <div className="p-2 bg-blue-600 rounded-lg">
                  <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197M13 7a4 4 0 11-8 0 4 4 0 018 0z" />
                  </svg>
                </div>
                <div>
                  <CardTitle className="text-lg">All Users</CardTitle>
                  <p className="text-xs text-gray-600 mt-0.5">{users.length} total users</p>
                </div>
              </div>
            </CardHeader>
            <CardContent className="p-0">
              {users.length === 0 ? (
                <div className="text-center py-16">
                  <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                    <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197M13 7a4 4 0 11-8 0 4 4 0 018 0z" />
                    </svg>
                  </div>
                  <p className="text-gray-500 font-medium">No users found</p>
                  <p className="text-sm text-gray-400 mt-1">Get started by adding your first user</p>
                </div>
              ) : (
                <div className="divide-y divide-gray-100">
                  {users.map((u, idx) => (
                    <div key={u.id} className={`p-4 hover:bg-gray-50 transition-colors ${idx === 0 ? 'rounded-t-lg' : ''} ${idx === users.length - 1 ? 'rounded-b-lg' : ''}`}>
                      <div className="flex items-start gap-4">
                        {/* Avatar */}
                        <div className={`w-12 h-12 rounded-full flex items-center justify-center flex-shrink-0 text-white font-bold text-lg ${
                          u.role === 'admin' ? 'bg-gradient-to-br from-purple-500 to-purple-600' :
                          u.role === 'doctor' ? 'bg-gradient-to-br from-blue-500 to-blue-600' :
                          'bg-gradient-to-br from-gray-500 to-gray-600'
                        }`}>
                          {u.name.charAt(0).toUpperCase()}
                        </div>

                        {/* User Info */}
                        <div className="flex-1 min-w-0">
                          <div className="flex items-start justify-between gap-4">
                            <div className="min-w-0 flex-1">
                              <div className="flex items-center gap-2 mb-1">
                                <h3 className="font-semibold text-gray-900 text-base truncate">{u.name}</h3>
                                <span className={`px-2.5 py-0.5 rounded-full text-xs font-medium ${
                                  u.role === 'admin' ? 'bg-purple-100 text-purple-700 border border-purple-200' :
                                  u.role === 'doctor' ? 'bg-blue-100 text-blue-700 border border-blue-200' :
                                  'bg-gray-100 text-gray-700 border border-gray-200'
                                }`}>
                                  {u.role === 'admin' ? 'ðŸ‘‘ Admin' : u.role === 'doctor' ? 'ðŸ©º Doctor' : u.role}
                                </span>
                              </div>
                              <p className="text-sm text-gray-600 truncate flex items-center gap-1.5">
                                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                                </svg>
                                {u.email}
                              </p>
                              {(u as any).client && (
                                <p className="text-xs text-gray-500 mt-1 flex items-center gap-1">
                                  <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
                                  </svg>
                                  Client: {clientName((u as any).client)}
                                </p>
                              )}
                            </div>

                            {/* Doctor Image Limits */}
                            {u.role === 'doctor' && (
                              <div className="flex-shrink-0">
                                {editingLimits === u.id ? (
                                  <div className="bg-white border-2 border-blue-200 rounded-lg p-4 shadow-lg min-w-[260px]">
                                    <h4 className="text-sm font-semibold text-gray-900 mb-3">Edit Permissions</h4>
                                    <div className="space-y-3">
                                      <div>
                                        <label className="text-xs font-medium text-gray-700 block mb-1.5">Monthly Image Limit</label>
                                        <input
                                          type="number"
                                          value={limitForm.imageConversionLimit}
                                          onChange={(e) => setLimitForm({ ...limitForm, imageConversionLimit: parseInt(e.target.value) })}
                                          className="w-full px-3 py-2 border border-blue-300 rounded-lg text-sm text-gray-900 bg-white focus:ring-2 focus:ring-blue-500 focus:border-blue-500 focus:outline-none"
                                          placeholder="e.g., 10"
                                        />
                                      </div>
                                      <div className="flex items-center gap-2 p-2 bg-gray-50 rounded-lg">
                                        <input
                                          type="checkbox"
                                          checked={limitForm.canShowImprovementPlans}
                                          onChange={(e) => setLimitForm({ ...limitForm, canShowImprovementPlans: e.target.checked })}
                                          id={`improvement-${u.id}`}
                                          className="w-4 h-4 text-blue-600 rounded focus:ring-2 focus:ring-blue-500"
                                        />
                                        <label htmlFor={`improvement-${u.id}`} className="text-xs font-medium text-gray-700 cursor-pointer">
                                          Allow Improvement Plans
                                        </label>
                                      </div>
                                      <div className="flex gap-2 pt-2">
                                        <Button 
                                          onClick={() => saveLimits(u.id)} 
                                          className="flex-1 text-xs px-3 py-2 bg-green-600 hover:bg-green-700 text-white rounded-lg"
                                        >
                                          ðŸ’¾ Save
                                        </Button>
                                        <Button 
                                          onClick={() => setEditingLimits(null)} 
                                          className="flex-1 text-xs px-3 py-2 bg-gray-400 hover:bg-gray-500 text-white rounded-lg"
                                        >
                                          Cancel
                                        </Button>
                                      </div>
                                    </div>
                                  </div>
                                ) : (
                                  <div className="bg-gradient-to-br from-blue-50 to-indigo-50 border border-blue-200 rounded-lg p-3 min-w-[180px]">
                                    <div className="text-xs text-gray-600 mb-1 font-medium">Image Usage</div>
                                    <div className="flex items-center gap-2 mb-2">
                                      <div className="text-2xl font-bold text-blue-600">{(u as any).imageConversionsUsed || 0}</div>
                                      <div className="text-sm text-gray-500">/ {(u as any).imageConversionLimit || 10}</div>
                                    </div>
                                    <div className="flex items-center gap-1 text-xs mt-2">
                                      {(u as any).canShowImprovementPlans !== false ? (
                                        <span className="text-green-600 font-medium">âœ“ Plans Enabled</span>
                                      ) : (
                                        <span className="text-red-600 font-medium">âœ— Plans Disabled</span>
                                      )}
                                    </div>
                                  </div>
                                )}
                              </div>
                            )}
                          </div>

                          {/* Actions */}
                          {u.role === 'doctor' && editingLimits !== u.id && (
                            <div className="flex gap-2 mt-3">
                              <Button
                                onClick={() => loadDoctorPatients(u.id, u.name)}
                                className="text-xs px-3 py-1.5 bg-blue-600 hover:bg-blue-700 text-white rounded-lg shadow-sm hover:shadow transition-all flex items-center gap-1.5"
                              >
                                <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
                                </svg>
                                View Patients
                              </Button>
                              <Button
                                onClick={() => openLimitEditor(u)}
                                className="text-xs px-3 py-1.5 bg-orange-600 hover:bg-orange-700 text-white rounded-lg shadow-sm hover:shadow transition-all flex items-center gap-1.5"
                              >
                                <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37 2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z" />
                                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                                </svg>
                                Manage Limits
                              </Button>
                              <Button
                                onClick={() => resetUsage(u.id)}
                                className="text-xs px-3 py-1.5 bg-red-600 hover:bg-red-700 text-white rounded-lg shadow-sm hover:shadow transition-all flex items-center gap-1.5"
                              >
                                <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                                </svg>
                                Reset Usage
                              </Button>
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
        </div>

        {/* Doctor's Patients Panel */}
        <div className="lg:col-span-1">
          <Card className="sticky top-4 shadow-lg border-0">
            <CardHeader className="bg-gradient-to-r from-green-50 to-emerald-50 border-b">
              <div className="flex items-center gap-3">
                <div className="p-2 bg-green-600 rounded-lg">
                  <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
                  </svg>
                </div>
                <div className="flex-1 min-w-0">
                  <CardTitle className="text-lg truncate">
                    {selectedDoctor ? `${selectedDoctor.name}'s Patients` : 'Doctor Patients'}
                  </CardTitle>
                  {selectedDoctor && (
                    <p className="text-xs text-gray-600 mt-0.5">
                      {selectedDoctor.patients.length} patient{selectedDoctor.patients.length !== 1 ? 's' : ''}
                    </p>
                  )}
                </div>
              </div>
            </CardHeader>
            <CardContent className="p-4">
              {isLoadingPatients ? (
                <div className="text-center py-12">
                  <div className="animate-spin w-10 h-10 border-4 border-green-600 border-t-transparent rounded-full mx-auto mb-3"></div>
                  <p className="text-sm text-gray-600 font-medium">Loading patients...</p>
                  <p className="text-xs text-gray-400 mt-1">Please wait</p>
                </div>
              ) : selectedDoctor ? (
                selectedDoctor.patients.length === 0 ? (
                  <div className="text-center py-12">
                    <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
                      <svg className="w-8 h-8 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
                      </svg>
                    </div>
                    <p className="text-sm font-medium text-gray-900">No patients yet</p>
                    <p className="text-xs text-gray-500 mt-1">This doctor hasn't been assigned any patients</p>
                  </div>
                ) : (
                  <div className="space-y-2 max-h-[calc(100vh-280px)] overflow-y-auto pr-2">
                    {selectedDoctor.patients.map((patient: any, index: number) => (
                      <div 
                        key={patient._id || index} 
                        className="group p-3 bg-gradient-to-r from-gray-50 to-gray-100 hover:from-green-50 hover:to-emerald-50 rounded-lg transition-all duration-200 border border-transparent hover:border-green-200 cursor-pointer"
                      >
                        <div className="flex items-center gap-3">
                          <div className="relative">
                            <div className="w-10 h-10 bg-gradient-to-br from-green-500 to-emerald-600 rounded-full flex items-center justify-center flex-shrink-0 shadow-md">
                              <span className="text-white text-sm font-bold">
                                {patient.name?.charAt(0).toUpperCase() || '?'}
                              </span>
                            </div>
                            <div className="absolute -bottom-0.5 -right-0.5 w-3 h-3 bg-green-500 border-2 border-white rounded-full"></div>
                          </div>
                          <div className="flex-1 min-w-0">
                            <p className="font-semibold text-gray-900 text-sm truncate group-hover:text-green-700 transition-colors">
                              {patient.name}
                            </p>
                            {patient.email && (
                              <p className="text-xs text-gray-600 truncate flex items-center gap-1 mt-0.5">
                                <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                                </svg>
                                {patient.email}
                              </p>
                            )}
                            {patient.contact && (
                              <p className="text-xs text-gray-500 flex items-center gap-1 mt-0.5">
                                <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z" />
                                </svg>
                                {patient.contact}
                              </p>
                            )}
                          </div>
                          <svg className="w-4 h-4 text-gray-400 group-hover:text-green-600 transition-colors" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                          </svg>
                        </div>
                      </div>
                    ))}
                  </div>
                )
              ) : (
                <div className="text-center py-16">
                  <div className="w-20 h-20 bg-gradient-to-br from-green-100 to-emerald-100 rounded-full flex items-center justify-center mx-auto mb-4 shadow-inner">
                    <svg className="w-10 h-10 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4.354a4 4 0 110 5.292M15 21H3v-1a6 6 0 0112 0v1zm0 0h6v-1a6 6 0 00-9-5.197M13 7a4 4 0 11-8 0 4 4 0 018 0z" />
                    </svg>
                  </div>
                  <p className="text-sm font-medium text-gray-900 mb-1">Select a doctor to view patients</p>
                  <p className="text-xs text-gray-500">Click "View Patients" to see their assigned patients</p>
                  <div className="mt-4 flex items-center justify-center gap-2 text-xs text-gray-400">
                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 11l5-5m0 0l5 5m-5-5v12" />
                    </svg>
                    <span>Look left for the button</span>
                  </div>
                </div>
              )}
            </CardContent>
          </Card>
        </div>
      </div>

      {/* Custom Modal */}
      <Modal
        isOpen={modal.isOpen}
        onClose={closeModal}
        title={modal.title}
        message={modal.message}
        type={modal.type}
        onConfirm={modal.onConfirm}
        confirmText={modal.type === 'confirm' ? 'Yes, Reset' : 'OK'}
        cancelText="Cancel"
      />
    </div>
  );
}
